/**=====================
     responsive css start
==========================**/
@media (min-width: 1430px) {
    .container {
        max-width: 1400px;
    }
}

@media screen and (max-width: 400px) {
    .sticky-bottom-cart {
        .cart-content {
            display: block;

            .add-btn {
                .outline-button {
                    width: 100%;
                    margin-top: 12px;
                }
            }
        }
    }
}